<template>
  <div class="ticker-tape-container">
    <div class="ticker-tape-header">
      <h4>Market Ticker</h4>
      <span class="ticker-info">Live market data</span>
    </div>
    <div class="ticker-tape-widget" :id="widgetId" ref="container">
      <div v-if="!loaded && !error" class="ticker-loading">
        <div class="loading-spinner"></div>
        <span>Loading market ticker...</span>
      </div>
      
      <div v-if="error" class="ticker-error">
        <span> Failed to load ticker</span>
        <button @click="retry" class="retry-btn">Retry</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TradingViewTickerTape',
  props: {
    symbols: {
      type: Array,
      default: () => [
        'FOREXCOM:SPXUSD',
        'FOREXCOM:NSXUSD', 
        'BITSTAMP:BTCUSD',
        'NASDAQ:QQQ',
        'TVC:GOLD',
        'EASYMARKETS:OILUSD'
      ]
    },
    lineChartType: {
      type: String,
      default: 'Baseline'
    },
    showHover: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      loaded: false,
      error: false,
      widgetId: `ticker-tape-${Date.now()}`
    }
  },
  mounted() {
    this.loadTickerTape()
  },
  methods: {
    async loadTickerTape() {
      this.loaded = false
      this.error = false

      try {
        //  TradingView ticker tape 
        if (!window.TradingViewTickerTape) {
          await this.loadTickerScript()
        }

        //  DOM 
        await this.$nextTick()
        
        //  ticker tape 
        this.createTickerTape()
        
      } catch (err) {
        console.error('Failed to load ticker tape:', err)
        this.error = true
      }
    },

    async loadTickerScript() {
      return new Promise((resolve, reject) => {
        // 
        const existingScript = document.querySelector('script[src*="tv-ticker-tape.js"]')
        if (existingScript) {
          resolve()
          return
        }

        const script = document.createElement('script')
        script.type = 'module'
        script.src = 'https://widgets.tradingview-widget.com/w/en/tv-ticker-tape.js'
        script.async = true
        
        script.onload = () => {
          //  custom element 
          setTimeout(resolve, 100)
        }
        
        script.onerror = () => {
          reject(new Error('Failed to load TradingView ticker tape script'))
        }
        
        document.head.appendChild(script)
      })
    },

    createTickerTape() {
      const container = this.$refs.container
      if (!container) return

      // 
      const existingTicker = container.querySelector('tv-ticker-tape')
      if (existingTicker) {
        existingTicker.remove()
      }

      //  ticker tape 
      const tickerTape = document.createElement('tv-ticker-tape')
      
      // 
      tickerTape.setAttribute('symbols', this.symbols.join(','))
      tickerTape.setAttribute('line-chart-type', this.lineChartType)
      
      if (this.showHover) {
        tickerTape.setAttribute('show-hover', '')
      }

      // 
      tickerTape.addEventListener('load', () => {
        this.loaded = true
        console.log('TradingView Ticker Tape loaded successfully')
      })

      tickerTape.addEventListener('error', () => {
        this.error = true
        console.error('TradingView Ticker Tape failed to load')
      })

      // 
      container.appendChild(tickerTape)
      
      // 
      setTimeout(() => {
        if (!this.loaded && !this.error) {
          this.loaded = true //  loading
        }
      }, 5000)
    },

    async retry() {
      await this.loadTickerTape()
    }
  }
}
</script>

<style scoped>
.ticker-tape-container {
  background: white;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1.5rem;
}

.ticker-tape-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #f0f0f0;
}

.ticker-tape-header h4 {
  font-size: 1rem;
  font-weight: 600;
  color: #333;
  margin: 0;
}

.ticker-info {
  font-size: 0.8rem;
  color: #666;
  font-style: italic;
}

.ticker-tape-widget {
  height: 80px;
  overflow: hidden;
  position: relative;
}

/* Loading  */
.ticker-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  height: 80px;
  color: #6c757d;
  font-size: 0.9rem;
}

.loading-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid #e9ecef;
  border-top: 2px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Error  */
.ticker-error {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  height: 80px;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 4px;
  color: #721c24;
}

.retry-btn {
  padding: 0.25rem 0.75rem;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: 500;
}

.retry-btn:hover {
  background: #c82333;
}

/* TradingView Ticker Tape  */
:global(tv-ticker-tape) {
  width: 1110px !important;
  height: 80px !important;
  display: block !important;
}

/*  */
@media (max-width: 768px) {
  .ticker-tape-container {
    padding: 0.75rem;
    margin-bottom: 1rem;
  }
  
  .ticker-tape-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }
  
  .ticker-tape-header h4 {
    font-size: 0.9rem;
  }
  
  .ticker-info {
    font-size: 0.75rem;
  }
}

@media (max-width: 480px) {
  .ticker-tape-container {
    padding: 0.5rem;
    margin: 0 -0.25rem 1rem -0.25rem;
  }
}
</style>