<template>
  <div class="cache-test">
    <h2>Symbols Cache Configuration Test</h2>
    <p class="text-muted">Testing the symbols cache configuration and timing</p>
    
    <div class="cache-info-panel">
      <h3>Current Cache Configuration</h3>
      <div class="info-grid">
        <div class="info-item">
          <span class="label">Cache Interval:</span>
          <span class="value">{{ cacheInfo.cacheIntervalMinutes }} minutes</span>
        </div>
        <div class="info-item">
          <span class="label">Sheets Timeout:</span>
          <span class="value">{{ cacheInfo.sheetsTimeoutMs }}ms</span>
        </div>
        <div class="info-item">
          <span class="label">Last Update:</span>
          <span class="value">{{ formatLastUpdate() }}</span>
        </div>
        <div class="info-item">
          <span class="label">Cache Valid:</span>
          <span class="value" :class="{ 'valid': cacheInfo.cacheValid, 'invalid': !cacheInfo.cacheValid }">
            {{ cacheInfo.cacheValid ? 'Yes' : 'No' }}
          </span>
        </div>
        <div class="info-item">
          <span class="label">Next Update In:</span>
          <span class="value">{{ formatNextUpdate() }}</span>
        </div>
      </div>
    </div>

    <div class="symbols-panel">
      <h3>Current Symbols Configuration</h3>
      <div class="symbols-info">
        <div class="source-info">
          <span class="label">Source:</span>
          <span class="value source-{{ configSource }}">{{ configSource }}</span>
        </div>
        <div class="symbols-list">
          <span class="label">Symbols ({{ symbols.length }}):</span>
          <div class="symbols-tags">
            <span v-for="symbol in symbols" :key="symbol" class="symbol-tag">
              {{ symbol }}
            </span>
          </div>
        </div>
      </div>
    </div>

    <div class="actions-panel">
      <h3>Actions</h3>
      <div class="actions">
        <button @click="refreshConfig" :disabled="loading" class="action-btn">
          {{ loading ? 'Refreshing...' : 'Refresh Configuration' }}
        </button>
        <button @click="clearCache" class="action-btn secondary">
          Clear Cache
        </button>
        <button @click="loadCacheInfo" class="action-btn secondary">
          Reload Info
        </button>
      </div>
    </div>

    <div v-if="error" class="error-panel">
      <h3>Error</h3>
      <p class="error-message">{{ error }}</p>
    </div>
  </div>
</template>

<script>
import { symbolsConfig } from '@/utils/symbolsConfig'

export default {
  name: 'CacheTest',
  data() {
    return {
      cacheInfo: {},
      symbols: [],
      configSource: 'static',
      loading: false,
      error: null
    }
  },
  async mounted() {
    await this.loadCacheInfo()
  },
  methods: {
    async loadCacheInfo() {
      try {
        this.cacheInfo = symbolsConfig.getCacheInfo()
        this.symbols = await symbolsConfig.getSymbolsList()
        this.configSource = symbolsConfig.getConfigSource()
        this.error = null
      } catch (err) {
        this.error = String(err)
        console.error('Failed to load cache info:', err)
      }
    },

    async refreshConfig() {
      this.loading = true
      try {
        this.symbols = await symbolsConfig.refresh()
        this.configSource = symbolsConfig.getConfigSource()
        this.cacheInfo = symbolsConfig.getCacheInfo()
        this.error = null
      } catch (err) {
        this.error = String(err)
        console.error('Failed to refresh config:', err)
      } finally {
        this.loading = false
      }
    },

    clearCache() {
      symbolsConfig.cache.clear()
      symbolsConfig.lastUpdate = null
      this.loadCacheInfo()
    },

    formatLastUpdate() {
      if (!this.cacheInfo.lastUpdate) return 'Never'
      const date = new Date(this.cacheInfo.lastUpdate)
      return date.toLocaleString()
    },

    formatNextUpdate() {
      if (!this.cacheInfo.nextUpdateIn) return 'Now'
      const minutes = Math.ceil(this.cacheInfo.nextUpdateIn / (1000 * 60))
      return `${minutes} minutes`
    }
  }
}
</script>

<style scoped>
.cache-test {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.cache-info-panel,
.symbols-panel,
.actions-panel,
.error-panel {
  background: white;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 1.5rem;
  margin-bottom: 2rem;
}

.cache-info-panel h3,
.symbols-panel h3,
.actions-panel h3,
.error-panel h3 {
  margin: 0 0 1rem 0;
  color: #333;
  font-size: 1.1rem;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
}

.info-item {
  display: flex;
  justify-content: space-between;
  padding: 0.75rem;
  background: #f8f9fa;
  border-radius: 4px;
}

.label {
  font-weight: 600;
  color: #495057;
}

.value {
  font-weight: 500;
}

.value.valid {
  color: #28a745;
}

.value.invalid {
  color: #dc3545;
}

.symbols-info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.source-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.source-environment {
  color: #007bff;
  font-weight: 600;
}

.source-cache {
  color: #28a745;
  font-weight: 600;
}

.source-google_sheets {
  color: #ffc107;
  font-weight: 600;
}

.source-static {
  color: #6c757d;
  font-weight: 600;
}

.symbols-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.symbols-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.symbol-tag {
  background: #e7f3ff;
  color: #007bff;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.85rem;
  font-weight: 500;
}

.actions {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.action-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.action-btn:not(.secondary) {
  background: #007bff;
  color: white;
}

.action-btn:not(.secondary):hover:not(:disabled) {
  background: #0056b3;
}

.action-btn.secondary {
  background: #6c757d;
  color: white;
}

.action-btn.secondary:hover {
  background: #545b62;
}

.action-btn:disabled {
  background: #e9ecef;
  color: #6c757d;
  cursor: not-allowed;
}

.error-panel {
  background: #f8d7da;
  border-color: #f5c6cb;
}

.error-message {
  color: #721c24;
  margin: 0;
  font-family: monospace;
  font-size: 0.9rem;
}

.text-muted {
  color: #6c757d;
  margin-bottom: 2rem;
}

@media (max-width: 768px) {
  .cache-test {
    padding: 1rem;
  }
  
  .info-grid {
    grid-template-columns: 1fr;
  }
  
  .actions {
    flex-direction: column;
  }
  
  .symbols-tags {
    justify-content: flex-start;
  }
}
</style>