<template>
  <div class="stock-detail">
    <!-- Breadcrumb -->
    <nav class="breadcrumb">
      <router-link to="/stock-dashboard" class="breadcrumb-link">Stock Dashboard</router-link>
      <span class="breadcrumb-separator"></span>
      <span class="breadcrumb-current">{{ symbol }} Analysis</span>
    </nav>

    <!-- Stock Header -->
    <div class="stock-header">
      <div class="stock-info-header">
        <div class="symbol-info">
          <h3 class="symbol">{{ symbol }}</h3>
          <div class="symbol-tags">
            <span class="exchange-tag">{{ exchange }}</span>
            <span class="industry-tag" :class="`industry-${getIndustryCategory()}`">{{ getIndustry() }}</span>
          </div>
        </div>
        <div class="header-actions">
          <button @click="goBack" class="detail-btn">
            <span class="btn-icon"></span>
            Back to Stock Dashboard
          </button>
        </div>
      </div>

      <!-- TradingView Widgets Container - Same as StockCard -->
      <div class="widgets-container">
        <!-- Symbol Overview (2/3 width) -->
        <div class="widget-overview">
          <div class="widget-header">
            <h4>Symbol Overview</h4>
          </div>
          <FastTradingViewWidget 
            widget-type="overview"
            :symbol="symbol" 
            :exchange="exchange"
          />
        </div>

        <!-- Technical Analysis (1/3 width) -->
        <div class="widget-technical">
          <div class="widget-header">
            <h4>Technical Analysis</h4>
          </div>
          <FastTradingViewWidget 
            widget-type="technical"
            :symbol="symbol" 
            :exchange="exchange"
          />
        </div>
      </div>
    </div>

    <!-- Main Content Layout -->
    <div class="content-layout">
      <!-- Row 1: Company Profile (Full Width) -->
      <div class="company-profile-row">
        <div class="widget-container">
          <h3 class="widget-title">Company Profile</h3>
          <TradingViewCompanyProfile :symbol="symbol" :exchange="exchange" />
        </div>
      </div>

      <!-- Row 2: Fundamental Data (Full Width) -->
      <div class="fundamental-data-row">
        <div class="widget-container">
          <h3 class="widget-title">Fundamental Data</h3>
          <TradingViewFundamentalData :symbol="symbol" :exchange="exchange" />
        </div>
      </div>
    </div>

    <!-- Loading State -->
    <div v-if="loading" class="loading-overlay">
      <div class="loading-spinner">
        <div class="spinner"></div>
        <p>Loading {{ symbol }} analysis...</p>
      </div>
    </div>
  </div>
</template>

<script>
import FastTradingViewWidget from '@/components/FastTradingViewWidget.vue'
import TradingViewCompanyProfile from '@/components/TradingViewCompanyProfile.vue'
import TradingViewFundamentalData from '@/components/TradingViewFundamentalData.vue'

export default {
  name: 'StockDetail',
  components: {
    FastTradingViewWidget,
    TradingViewCompanyProfile,
    TradingViewFundamentalData
  },
  data() {
    return {
      loading: true
    }
  },
  computed: {
    symbol() {
      return this.$route.params.symbol || 'RKLB'
    },
    exchange() {
      //  symbol 
      const symbol = this.symbol
      if (['AAPL', 'MSFT', 'GOOGL', 'AMZN', 'TSLA', 'NVDA', 'META', 'NFLX', 'RKLB', 'ASTS', 'RIVN', 'MDB', 'ONDS', 'PL', 'AVAV', 'RDW'].includes(symbol)) {
        return 'NASDAQ'
      } else if (['TSM', 'ORCL'].includes(symbol)) {
        return 'NYSE'
      }
      return 'NASDAQ'
    }
  },
  mounted() {
    // 
    setTimeout(() => {
      this.loading = false
    }, 1000)
  },
  methods: {
    getSector() {
      // Mock metadata for demonstration - in real app, this would come from props or API
      const mockMetadata = {
        'ASTS': { sector: 'Technology', industry: 'Satellite Communications', confidence: 0.85 },
        'AVAV': { sector: 'Industrials', industry: 'Aerospace & Defense', confidence: 0.90 },
        'RKLB': { sector: 'Technology', industry: 'Space Infrastructure', confidence: 0.88 }
      }
      
      const metadata = mockMetadata[this.symbol]
      if (!metadata || metadata.confidence < 0.7) {
        return 'Unknown'
      }
      
      return metadata.sector || 'Unknown'
    },

    getIndustry() {
      const mockMetadata = {
        'ASTS': { sector: 'Technology', industry: 'Satellite Communications', confidence: 0.85 },
        'AVAV': { sector: 'Industrials', industry: 'Aerospace & Defense', confidence: 0.90 },
        'RKLB': { sector: 'Technology', industry: 'Space Infrastructure', confidence: 0.88 }
      }
      
      const metadata = mockMetadata[this.symbol]
      if (!metadata || metadata.confidence < 0.7) {
        return 'Unknown Industry'
      }
      
      return metadata.industry || metadata.sector || 'Unknown Industry'
    },

    getIndustryCategory() {
      const industry = this.getIndustry()
      
      const industryCategories = {
        'Industrial IoT Solutions': 'tech-iot',
        'Satellite Communications': 'tech-satellite',
        'Database Software': 'tech-software',
        'Enterprise Software': 'tech-software',
        'Semiconductors': 'tech-hardware',
        'Aerospace & Defense': 'industrial-aerospace',
        'Space Infrastructure': 'industrial-space',
        'Satellite Communications': 'communications',
        'Electric Vehicles': 'automotive',
        'Unknown Industry': 'unknown'
      }
      
      return industryCategories[industry] || 'other'
    },

    goBack() {
      this.$router.push({ name: 'stock-dashboard' })
    }
  }
}
</script>

