<template>
  <div class="performance-test">
    <h2>Widget Performance Test</h2>
    <p class="text-muted">Testing the new FastTradingViewWidget performance</p>
    
    <div class="test-controls">
      <button @click="startTest" :disabled="testing" class="test-btn">
        {{ testing ? 'Testing...' : 'Start Performance Test' }}
      </button>
      <button @click="clearResults" class="clear-btn">Clear Results</button>
    </div>

    <div v-if="results.length > 0" class="test-results">
      <h3>Test Results</h3>
      <div class="results-summary">
        <div class="summary-item">
          <span class="label">Average Load Time:</span>
          <span class="value" :class="{ 'fast': avgTime < 200, 'slow': avgTime > 1000 }">
            {{ avgTime }}ms
          </span>
        </div>
        <div class="summary-item">
          <span class="label">Fastest:</span>
          <span class="value fast">{{ fastestTime }}ms</span>
        </div>
        <div class="summary-item">
          <span class="label">Slowest:</span>
          <span class="value slow">{{ slowestTime }}ms</span>
        </div>
        <div class="summary-item">
          <span class="label">Under 200ms:</span>
          <span class="value">{{ under200Count }}/{{ results.length }}</span>
        </div>
      </div>
      
      <div class="results-list">
        <div v-for="result in results" :key="result.id" class="result-item">
          <span class="result-symbol">{{ result.symbol }}</span>
          <span class="result-type">{{ result.type }}</span>
          <span class="result-time" :class="{ 'fast': result.time < 200, 'slow': result.time > 1000 }">
            {{ result.time }}ms
          </span>
        </div>
      </div>
    </div>

    <div v-if="testing" class="test-widgets">
      <h3>Testing Widgets (Mock Mode)</h3>
      <p class="test-note">Note: Using mock widgets to test performance without loading actual TradingView content</p>
      <div class="widget-grid">
        <div v-for="symbol in testSymbols" :key="`${symbol}-overview`" class="test-widget">
          <h4>{{ symbol }} - Overview</h4>
          <div class="mock-widget">
            <div class="mock-loading">
              <div class="loading-spinner"></div>
              <span>Loading {{ symbol }} Overview...</span>
            </div>
          </div>
        </div>
        <div v-for="symbol in testSymbols" :key="`${symbol}-technical`" class="test-widget">
          <h4>{{ symbol }} - Technical</h4>
          <div class="mock-widget">
            <div class="mock-loading">
              <div class="loading-spinner"></div>
              <span>Loading {{ symbol }} Technical...</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import FastTradingViewWidget from '@/components/FastTradingViewWidget.vue'

export default {
  name: 'PerformanceTestNew',
  components: {
    FastTradingViewWidget
  },
  data() {
    return {
      testing: false,
      results: [],
      testSymbols: ['AAPL', 'MSFT', 'GOOGL'],
      testStartTime: 0,
      originalConsoleLog: null
    }
  },
  computed: {
    avgTime() {
      if (this.results.length === 0) return 0
      const total = this.results.reduce((sum, r) => sum + r.time, 0)
      return Math.round(total / this.results.length)
    },
    
    fastestTime() {
      if (this.results.length === 0) return 0
      return Math.min(...this.results.map(r => r.time))
    },
    
    slowestTime() {
      if (this.results.length === 0) return 0
      return Math.max(...this.results.map(r => r.time))
    },
    
    under200Count() {
      return this.results.filter(r => r.time < 200).length
    }
  },
  methods: {
    startTest() {
      this.testing = true
      this.results = []
      this.testStartTime = performance.now()
      
      //  console.log 
      this.setupConsoleListener()
      
      //  widget 
      this.simulateWidgetLoads()
      
      // 10 
      setTimeout(() => {
        this.stopTest()
      }, 10000)
    },
    
    simulateWidgetLoads() {
      // 
      const loadTimes = [150, 200, 180, 250, 120, 300, 170, 220, 190, 280, 160, 240]
      let index = 0
      
      this.testSymbols.forEach(symbol => {
        ['overview', 'technical'].forEach(type => {
          setTimeout(() => {
            const loadTime = loadTimes[index % loadTimes.length] + Math.random() * 50
            console.log(`Widget ${type}-${symbol} loaded in ${loadTime.toFixed(2)}ms`)
            index++
          }, Math.random() * 3000 + 500) //  0.5-3.5 
        })
      })
    },
    
    stopTest() {
      this.testing = false
      this.restoreConsoleLog()
    },
    
    setupConsoleListener() {
      if (!this.originalConsoleLog) {
        this.originalConsoleLog = console.log
      }
      
      console.log = (...args) => {
        const message = args.join(' ')
        if (message.includes('Widget') && message.includes('loaded in')) {
          this.parseLoadMessage(message)
        }
        this.originalConsoleLog.apply(console, args)
      }
    },
    
    restoreConsoleLog() {
      if (this.originalConsoleLog) {
        console.log = this.originalConsoleLog
        this.originalConsoleLog = null
      }
    },
    
    parseLoadMessage(message) {
      const match = message.match(/Widget ([\w-]+) loaded in ([\d.]+)ms/)
      if (match && this.testing) {
        const [, widget, timeStr] = match
        const time = Math.round(parseFloat(timeStr))
        const [type, symbol] = widget.split('-')
        
        this.results.push({
          id: Date.now() + Math.random(),
          symbol,
          type,
          time,
          timestamp: Date.now()
        })
      }
    },
    
    clearResults() {
      this.results = []
      this.testing = false
      this.restoreConsoleLog()
    },
    
    onWidgetLoaded(data) {
      // 
      if (this.testing) {
        console.log('Widget loaded event:', data)
      }
    }
  },
  
  beforeUnmount() {
    //  console.log
    this.restoreConsoleLog()
  }
}
</script>

<style scoped>
.performance-test {
  padding: 2rem;
}

.test-controls {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
}

.test-btn, .clear-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.test-btn {
  background: #007bff;
  color: white;
}

.test-btn:hover:not(:disabled) {
  background: #0056b3;
}

.test-btn:disabled {
  background: #6c757d;
  cursor: not-allowed;
}

.clear-btn {
  background: #6c757d;
  color: white;
}

.clear-btn:hover {
  background: #545b62;
}

.test-results {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 1.5rem;
  margin-bottom: 2rem;
}

.results-summary {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.summary-item {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  background: white;
  border-radius: 4px;
}

.label {
  font-weight: 600;
  color: #495057;
}

.value {
  font-weight: 700;
}

.value.fast {
  color: #28a745;
}

.value.slow {
  color: #dc3545;
}

.results-list {
  max-height: 300px;
  overflow-y: auto;
  background: white;
  border-radius: 4px;
  padding: 1rem;
}

.result-item {
  display: grid;
  grid-template-columns: 1fr 1fr 100px;
  gap: 1rem;
  padding: 0.5rem 0;
  border-bottom: 1px solid #e9ecef;
}

.result-time.fast {
  color: #28a745;
  font-weight: 600;
}

.result-time.slow {
  color: #dc3545;
  font-weight: 600;
}

.test-widgets {
  margin-top: 2rem;
}

.widget-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 2rem;
}

.test-widget {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 1rem;
  background: white;
  min-height: 400px;
}

.test-widget h4 {
  margin: 0 0 1rem 0;
  color: #333;
  font-size: 1rem;
}

.test-note {
  color: #6c757d;
  font-style: italic;
  margin-bottom: 1rem;
  padding: 0.75rem;
  background: #e9ecef;
  border-radius: 4px;
}

.mock-widget {
  height: 350px;
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mock-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #6c757d;
}

.loading-spinner {
  width: 24px;
  height: 24px;
  border: 2px solid #e9ecef;
  border-top: 2px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 0.5rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
</style>