<template>
  <div class="stock-overview">
    <div class="stock-header">
      <div class="config-info">
        <span class="config-source">Symbols from: {{ configSource }}</span>
        <span class="symbols-count">({{ configuredSymbols.length }} configured)</span>
        <span class="cache-info" v-if="cacheInfo.cacheIntervalMinutes">
          Cache: {{ cacheInfo.cacheIntervalMinutes }}min
        </span>
      </div>
      <div class="update-info" v-if="lastUpdate">
        <span class="text-muted">Last updated: {{ formatTime(lastUpdate) }}</span>
      </div>
    </div>



    <div v-if="loading" class="loading">
      Loading stock data...
    </div>

    <div v-else-if="error" class="error">
      <p class="text-danger">{{ error }}</p>
      <button @click="refresh" class="btn btn-secondary">Retry</button>
    </div>

    <div v-else>
      <!-- Ticker Tape Section -->
      <TradingViewTickerTape 
        :symbols="tickerSymbols"
        line-chart-type="Baseline"
        :show-hover="true"
      />
      
      <!-- Sector Groups -->
      <div class="sector-groups">
        <div 
          v-for="(group, sector) in groupedStocks" 
          :key="sector"
          class="sector-group"
        >
          <h4 class="sector-title">
            {{ sector }}
            <span class="stock-count">({{ group.length }})</span>
          </h4>
          
          <div class="stocks-in-group">
            <StockCard
              v-for="stock in group"
              :key="stock.quote.symbol"
              :quote="stock.quote"
              :daily-data="stock.dailyData"
              :metadata="stock.metadata"
            />
          </div>
        </div>
      </div>
    </div>

    <div v-if="!loading && Object.keys(groupedStocks).length === 0" class="no-data">
      <p>No stock data available</p>
    </div>
  </div>
</template>

<script>
import { dataFetcher } from '@/lib/fetcher'
import { symbolsConfig } from '@/utils/symbolsConfig'
import StockCard from './StockCard.vue'
import TradingViewTickerTape from './TradingViewTickerTape.vue'

export default {
  name: 'StockOverview',
  components: {
    StockCard,
    TradingViewTickerTape
  },
  data() {
    return {
      quotes: [],
      dailyData: null,
      metadata: null,
      loading: false,
      error: null,
      lastUpdate: null,
      staleLevel: 'fresh',
      configuredSymbols: [],
      configSource: 'static',
      cacheInfo: {}
    }
  },
  computed: {
    tickerSymbols() {
      // 
      const baseSymbols = [
        'FOREXCOM:SPXUSD',    // S&P 500
        'FOREXCOM:NSXUSD',    // NASDAQ 100
        'BITSTAMP:BTCUSD',    // Bitcoin
        'NASDAQ:QQQ',         // QQQ ETF
        'TVC:GOLD',           // Gold
        'EASYMARKETS:OILUSD'  // Oil
      ]
      
      //  symbols ticker 
      if (this.configuredSymbols.length > 0) {
        //  4  symbols  ticker 
        const configSymbols = this.configuredSymbols
          .slice(0, 4)
          .map(symbol => {
            //  exchange 
            const metadata = this.metadata?.items.find(m => m.symbol === symbol)
            const exchange = metadata?.exchange || 'NASDAQ'
            return `${exchange}:${symbol}`
          })
        
        //  symbols  symbols
        return [...baseSymbols, ...configSymbols]
      }
      
      return baseSymbols
    },

    groupedStocks() {
      if (!this.quotes.length || !this.metadata) return {}
      
      const groups = {}
      
      this.quotes.forEach(quote => {
        const symbolMetadata = this.metadata.items.find(m => m.symbol === quote.symbol)
        const symbolDailyData = this.dailyData?.per_symbol.find(d => d.symbol === quote.symbol)
        
        //  PRD confidence >= 0.7  sector
        let sector = 'Unknown'
        if (symbolMetadata && symbolMetadata.confidence >= 0.7) {
          sector = symbolMetadata.sector || 'Unknown'
        }
        
        if (!groups[sector]) {
          groups[sector] = []
        }
        
        groups[sector].push({
          quote,
          dailyData: symbolDailyData,
          metadata: symbolMetadata
        })
      })
      
      //  sector  symbol A-Z
      const sortedGroups = {}
      Object.keys(groups)
        .sort() //  sector 
        .forEach(sector => {
          //  symbol A-Z 
          sortedGroups[sector] = groups[sector].sort((a, b) => 
            a.quote.symbol.toUpperCase().localeCompare(b.quote.symbol.toUpperCase())
          )
        })
      
      return sortedGroups
    }
  },
  async mounted() {
    await this.loadSymbolsConfig()
    await this.loadStockData()
  },
  methods: {
    async loadSymbolsConfig() {
      try {
        this.configuredSymbols = await symbolsConfig.getSymbolsList()
        this.configSource = symbolsConfig.getConfigSource()
        this.cacheInfo = symbolsConfig.getCacheInfo()
        console.log(`Loaded ${this.configuredSymbols.length} symbols from ${this.configSource}:`, this.configuredSymbols)
        console.log('Cache info:', this.cacheInfo)
      } catch (error) {
        console.warn('Failed to load symbols config:', error)
        this.configuredSymbols = symbolsConfig.getStaticSymbols()
        this.configSource = 'static'
        this.cacheInfo = symbolsConfig.getCacheInfo()
      }
    },

    async loadStockData() {
      this.loading = true
      this.error = null
      
      try {
        // 
        const [quotesResult, dailyResult, metadataResult] = await Promise.all([
          dataFetcher.fetchQuotesSnapshot(),
          dataFetcher.fetchDailySnapshot(),
          dataFetcher.fetchMetadataSnapshot()
        ])
        
        //  quotes -  symbols
        if (quotesResult.data && quotesResult.data.items) {
          //  symbols
          this.quotes = quotesResult.data.items.filter(quote => 
            this.configuredSymbols.includes(quote.symbol)
          )
          this.lastUpdate = quotesResult.as_of
          this.staleLevel = quotesResult.stale_level
        }
        
        //  daily data
        if (dailyResult.data) {
          this.dailyData = dailyResult.data
        }
        
        //  metadata
        if (metadataResult.data) {
          this.metadata = metadataResult.data
        }
        
        // 
        if (!this.quotes.length && !this.dailyData && !this.metadata) {
          throw new Error('No stock data available')
        }
        
      } catch (err) {
        this.error = String(err)
        console.error('Failed to load stock data:', err)
      } finally {
        this.loading = false
      }
    },

    async refresh() {
      await this.loadSymbolsConfig()
      await this.loadStockData()
    },

    formatTime(timeString) {
      if (!timeString) return ''
      
      try {
        const date = new Date(timeString)
        return date.toLocaleString('en-US', {
          month: 'short',
          day: 'numeric',
          hour: '2-digit',
          minute: '2-digit'
        })
      } catch {
        return timeString
      }
    }
  }
}
</script>

<style scoped>
.stock-overview {
  margin-bottom: 1rem;
}

.stock-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.75rem;
}

.config-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.85rem;
}

.config-source {
  color: #007bff;
  font-weight: 500;
}

.symbols-count {
  color: #6c757d;
  font-style: italic;
}

.cache-info {
  color: #28a745;
  font-weight: 500;
  font-size: 0.8rem;
}

.update-info {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 0.9rem;
}

.sector-groups {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.sector-group {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 1.5rem;
}

.sector-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.stock-count {
  font-size: 0.8rem;
  color: #666;
  font-weight: 400;
}

/* Stock Cards Grid Layout */
.stocks-in-group {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/*  StockCard  widgets */
.stocks-in-group > * {
  width: 100%;
}

/*  stocks-in-group  hover  */
.stocks-in-group:hover {
  /*  hover  */
}

.loading {
  text-align: center;
  padding: 3rem;
  color: #666;
  font-size: 1.1rem;
}

.error {
  text-align: center;
  padding: 2rem;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 8px;
  margin: 1rem 0;
}

.no-data {
  text-align: center;
  padding: 3rem;
  color: #666;
  background-color: #f8f9fa;
  border-radius: 8px;
}

/*  */

/*  () - 1200px  */
@media (min-width: 1200px) {
  .stocks-in-group {
    gap: 2.5rem;
  }
  
  .sector-group {
    padding: 2rem;
  }
}

/*  () - 768px  1199px */
@media (min-width: 768px) and (max-width: 1199px) {
  .stocks-in-group {
    gap: 2rem;
  }
  
  .sector-group {
    padding: 1.5rem;
  }
}

/*  () - 768px  */
@media (max-width: 767px) {
  .stock-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .update-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }
  
  .stocks-in-group {
    gap: 1.5rem;
  }
  
  .sector-group {
    padding: 1rem;
    margin: 0 -0.5rem; /*  sector group  */
  }
  
  .sector-title {
    font-size: 1rem;
  }
}

/*  () - 480px  */
@media (max-width: 480px) {
  .stock-overview {
    margin-bottom: 1rem;
  }
  
  .sector-groups {
    gap: 1.5rem;
  }
  
  .sector-group {
    padding: 0.75rem;
    margin: 0 -0.25rem;
  }
  
  .stocks-in-group {
    gap: 1rem;
  }
}
</style>