import { createApp } from 'vue'
import { createRouter, createWebHashHistory } from 'vue-router'
import App from './App.vue'
import './style.css'

// Import performance utilities
import '@/utils/widgetPreloader'

// Import pages
import MarketDashboard from './pages/MarketDashboard.vue'
import StockDashboard from './pages/StockDashboard.vue'
import Settings from './pages/Settings.vue'
import StockDetail from './pages/StockDetail.vue'
import PerformanceTestNew from './pages/PerformanceTestNew.vue'
import CacheTest from './pages/CacheTest.vue'
import TopStoriesTest from './pages/TopStoriesTest.vue'
import StockMarketInsightTest from './pages/StockMarketInsightTest.vue'

// Router configuration
const routes = [
  { path: '/', redirect: '/market-dashboard' },
  { path: '/market-dashboard', component: MarketDashboard, name: 'market-dashboard' },
  { path: '/stock-dashboard', component: StockDashboard, name: 'stock-dashboard' },
  { path: '/settings', component: Settings, name: 'settings' },
  { path: '/stock/:symbol', component: StockDetail, name: 'stock-detail' },
  // Development/Testing pages
  { path: '/performance-test', component: PerformanceTestNew, name: 'performance-test' },
  { path: '/cache-test', component: CacheTest, name: 'cache-test' },
  { path: '/top-stories-test', component: TopStoriesTest, name: 'top-stories-test' },
  { path: '/stock-market-insight-test', component: StockMarketInsightTest, name: 'stock-market-insight-test' }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

const app = createApp(App)
app.use(router)
app.mount('#app')
